  /* EFECTOS PALABRAS */

.name{
    font-size: 2em;
    position: relative;
    width: 9em;
    top:30%;
    padding: 2px 5px;
    margin: 2px auto; 
    font-family: "Molle-Regular"
}
.name .melod{
    width: 3.3em;
    margin : 0 0 0 .9em
}
.name div {
    margin:0px;
    display:inline-block;
    overflow:hidden;
    white-space:nowrap;
}

.name div:first-of-type {   
  -webkit-animation: showup 7s infinite;   
          animation: showup 7s infinite;
}

.melod:last-of-type, .ryth:last-of-type  {
  width: 2px;
  -webkit-animation: reveal 7s infinite;
          animation: reveal 7s infinite;
}

div:last-of-type span {
  margin-left:-835px;
  -webkit-animation: slidein 7s infinite;
          animation: slidein 7s infinite;
}

@-webkit-keyframes showup {
    0% {opacity:0;}
    20% {opacity:1;}
    80% {opacity:1;}
    100% {opacity:0;}
}

@keyframes showup {
    0% {opacity:0;}
    20% {opacity:1;}
    80% {opacity:1;}
    100% {opacity:0;}
}

@-webkit-keyframes slidein {
    0% { margin-left:-850px; }
    20% { margin-left:-850px; }
    35% { margin-left:0px; }
    100% { margin-left:0px; }
}

@keyframes slidein {
    0% { margin-left:-850px; }
    20% { margin-left:-850px; }
    35% { margin-left:0px; }
    100% { margin-left:0px; }
}

@-webkit-keyframes reveal {
    0% {opacity:0;width:0px;}
    20% {opacity:1;width:0px;}
    30% {width:150px;}
    80% {opacity:1;}
    100% {opacity:0;width:150;}
}

@keyframes reveal {
    0% {opacity:0;width:0px;}
    20% {opacity:1;width:0px;}
    30% {width:150px;}
    80% {opacity:1;}
    100% {opacity:0;width:150;}
}

#preloader {
    text-align:center;
      position: fixed;
      top: 40%;
      left: 0;
      width: 100%;
      height: 30%;
  }

  #loader {
      display: block;
      position: relative;
      left: 47%;
      top: 5.1em;
      width: 150px;
      height: 150px;
      margin: -75px 0 0 -75px;
      border-radius: 50%;
      border: 3px solid transparent;
      border-top-color: #9370DB;
      -webkit-animation: spin 2s linear infinite;
              animation: spin 2s linear infinite;
  }
  #loader:before {
      content: "";
      position: absolute;
      top: 5px;
      left: 5px;
      right: 5px;
      bottom: 5px;
      border-radius: 50%;
      border: 3px solid transparent;
      border-top-color: #BA55D3;
      -webkit-animation: spin 3s linear infinite;
              animation: spin 3s linear infinite;
  }
  #loader:after {
      content: "";
      position: absolute;
      top: 15px;
      left: 15px;
      right: 15px;
      bottom: 15px;
      border-radius: 50%;
      border: 3px solid transparent;
      border-top-color: #FF00FF;
      -webkit-animation: spin 1.5s linear infinite;
              animation: spin 1.5s linear infinite;
  }
  @-webkit-keyframes spin {
      0%   {

          -webkit-transform: rotate(0deg);

                  transform: rotate(0deg);
      }
      100% {

          -webkit-transform: rotate(360deg);

                  transform: rotate(360deg);
      }
  }
  @keyframes spin {
      0%   {
          -webkit-transform: rotate(0deg);
          transform: rotate(0deg);
      }
      100% {
          -webkit-transform: rotate(360deg);
          transform: rotate(360deg);
      }
  }
 
