@import url('https://fonts.googleapis.com/css?family=Roboto:300');
.content{
    position: absolute;
    width: 100%;
    height: 100%;
}
  /* EFECTOS PALABRAS */

.name{
    font-size: 2em;
    position: relative;
    width: 9em;
    top:30%;
    padding: 2px 5px;
    margin: 2px auto; 
    font-family: "Molle-Regular"
}
.name .melod{
    width: 3.3em;
    margin : 0 0 0 .9em
}
.name div {
    margin:0px;
    display:inline-block;
    overflow:hidden;
    white-space:nowrap;
}

.name div:first-of-type {   
  animation: showup 7s infinite;
}

.melod:last-of-type, .ryth:last-of-type  {
  width: 2px;
  animation: reveal 7s infinite;
}

div:last-of-type span {
  margin-left:-835px;
  animation: slidein 7s infinite;
}

@keyframes showup {
    0% {opacity:0;}
    20% {opacity:1;}
    80% {opacity:1;}
    100% {opacity:0;}
}

@keyframes slidein {
    0% { margin-left:-850px; }
    20% { margin-left:-850px; }
    35% { margin-left:0px; }
    100% { margin-left:0px; }
}

@keyframes reveal {
    0% {opacity:0;width:0px;}
    20% {opacity:1;width:0px;}
    30% {width:150px;}
    80% {opacity:1;}
    100% {opacity:0;width:150;}
}

#preloader {
    text-align:center;
      position: fixed;
      top: 40%;
      left: 0;
      width: 100%;
      height: 30%;
  }

  #loader {
      display: block;
      position: relative;
      left: 47%;
      top: 5.1em;
      width: 150px;
      height: 150px;
      margin: -75px 0 0 -75px;
      border-radius: 50%;
      border: 3px solid transparent;
      border-top-color: #9370DB;
      animation: spin 2s linear infinite;
  }
  #loader:before {
      content: "";
      position: absolute;
      top: 5px;
      left: 5px;
      right: 5px;
      bottom: 5px;
      border-radius: 50%;
      border: 3px solid transparent;
      border-top-color: #BA55D3;
      animation: spin 3s linear infinite;
  }
  #loader:after {
      content: "";
      position: absolute;
      top: 15px;
      left: 15px;
      right: 15px;
      bottom: 15px;
      border-radius: 50%;
      border: 3px solid transparent;
      border-top-color: #FF00FF;
      animation: spin 1.5s linear infinite;
  }
  @-webkit-keyframes spin {
      0%   {

          transform: rotate(0deg);
      }
      100% {

          transform: rotate(360deg);
      }
  }
  @keyframes spin {
      0%   {
          -webkit-transform: rotate(0deg);
          -ms-transform: rotate(0deg);
          transform: rotate(0deg);
      }
      100% {
          -webkit-transform: rotate(360deg);
          -ms-transform: rotate(360deg);
          transform: rotate(360deg);
      }
  }
 
@media (min-width: 576px) { 
    .name{
        font-size: 3.5em;
    }
    .name .melod{
        margin : 0 0 0 1.2em
    }
    #loader {
        width: 170px;
        height: 170px;
    }
 }

@media (min-width: 1200px) { 
    .name{
        font-size: 5em;
        top:20%;
    }
    .name .melod{
        margin : 0 0 0 1.5em
    }
    #loader {
        width: 200px;
        height: 200px;
    }
 }